.profile__edit-name {
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .profile__edit-name {
    font-size: 27px;
    line-height: 33px;
  }
}
