.popup__container {
  width: 430px;
  height: 330px;
  background-color: #fff;
  border-radius: 10px;
  position: relative;
  display: flex;
  align-items: baseline;
  flex-direction: column;
}

@media screen and (max-width: 768px) {
  .popup__container {
    width: 282px;
    height: 322px;
  }
}
